.formClass {
  padding: 5rem;
  margin: 20vh auto;
  max-width: 400px;
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 20%), 0 10px 25px 0 rgb(0 0 0 / 20%);
  border-radius: 10px;
}

.formClass label {
  display: block;
  margin-bottom: 10px;
  font-size: 23px;
  font-weight: bold;
  color: #000;
}

.span {
  font-size: 45px;
  margin: 0 10px;
  color: rgb(252, 3, 3);
  font-weight: bold;
}

.formClass input {
  width: 90%;
  padding: 15px;
  font-family: inherit;
  font-size: 22px;
}

.formClass h1 {
  color: #333;
}

.formClass button {
  padding: 15px 50px;
  margin-top: 15px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  font-size: 20px;
  background-color: #12824C;
  color: #fff;
  font-weight: bold;
}